import React, { Component } from 'react';
import '../Styles/Footer.css';

class Footer extends Component {
  render() {
    return (
      <footer className="footer">
        <div className="flex-container">
          <div className="flex-item"></div>
        </div>
      </footer>
    );
  }
}

export default Footer;



